
















import Vue from "vue";
export default Vue.extend({
  props: {
    item: {
      type: Object,
      default: () => {
        return {};
      }
    }
  },
  data(){
    return{
      str:''
    }
  },
  methods:{
    goPlay(item,id){
      this.$router.push({
        path:'/playlist',
        query:{
          id:item.id,
          item:JSON.stringify(item)
          
        }
      })
    }
      
  },
  filters: {
    count(value) {
      if (value > 100000000) {
        return (value / 100000000).toFixed(1) + "亿";
      } else {
        return (value / 10000).toFixed(1) + "万";
      }
    },
  }
})
